<template>
  <div>
    <PageTitle
        :pre-title="processTitle"
        :title="equipmentTitle"
        has-date-range-picker
        @changedDate="onChangedDate"/>

    <div class="page-box">
      <PanelTitle title="센서 데이터 분석 위험도"/>
      <ChartBox :raw-data="riskEquipmentCrigHistory.list" chart-type="MAIN"/>

      <table class="default-table">
        <thead>
        <tr>
          <th class="w100">날짜</th>
          <th colspan="2" class="w150">위험도</th>
          <th v-for="(item, idx) in timeListData"
              :key="idx">
            {{ item }}시
          </th>
        </tr>
        </thead>
        <tbody>
        <template v-for="(day, index) in dayDataList">
          <tr :key="index + '_0'">
            <td rowspan="6">{{ dayjs(dateList[index]).format('MM/DD') }}</td>
            <td colspan="2">화재폭발</td>
            <td v-for="(item, idx) in day"
                :key="idx">
              <RiskLabel v-if="item && item.CRIG101" :text-data="item.CRIG101" :is-delete-step-suffix="true"/>
              <span v-else>-</span>
            </td>
          </tr>
          <tr :key="index + '_1'">
            <td colspan="2">질식/중독</td>
            <td v-for="(item, idx) in day"
                :key="idx">
              <RiskLabel v-if="item && item.CRIG102" :text-data="item.CRIG102" :is-delete-step-suffix="true"/>
              <span v-else>-</span>
            </td>
          </tr>
          <tr :key="index + '_2'">
            <td rowspan="4">이상원인</td>
            <td>온도</td>
            <td v-for="(item, idx) in day"
                :key="idx">
              <RiskLabel v-if="item && item.CRIG103" :text-data="item.CRIG103" :is-delete-step-suffix="true"/>
              <span v-else>-</span>
            </td>
          </tr>
          <tr :key="index + '_3'">
            <td>압력</td>
            <td v-for="(item, idx) in day"
                :key="idx">
              <RiskLabel v-if="item && item.CRIG104" :text-data="item.CRIG104" :is-delete-step-suffix="true"/>
              <span v-else>-</span>
            </td>
          </tr>
          <tr :key="index + '_4'">
            <td>유량</td>
            <td v-for="(item, idx) in day"
                :key="idx">
              <RiskLabel v-if="item && item.CRIG105" :text-data="item.CRIG105" :is-delete-step-suffix="true"/>
              <span v-else>-</span>
            </td>
          </tr>
          <tr :key="index + '_5'">
            <td>누출농도</td>
            <td v-for="(item, idx) in day"
                :key="idx">
              <RiskLabel v-if="item && item.CRIG106" :text-data="item.CRIG106" :is-delete-step-suffix="true"/>
              <span v-else>-</span>
            </td>
          </tr>
        </template>
        </tbody>
      </table>

      <PanelTitle title="예지보전 위험도"/>

      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2">날짜</th>
          <th rowspan="2">위험 단계</th>
          <th rowspan="2">고장확률</th>
          <th rowspan="2">{{ partTitles.PART102 }}</th>
          <th rowspan="2">{{ partTitles.PART103 }}</th>
          <th colspan="3">이상원인</th>
        </tr>
        <tr>
          <th>{{ partTitles.PART104 }}</th>
          <th>{{ partTitles.PART105 }}</th>
          <th>{{ partTitles.PART106 }}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, index) in partDataList" :key="index">
          <td>{{ dayjs(item.PART101.ymd).format('M/DD') }}</td>
          <td>
            <RiskLabel
                v-if="item.PART101"
                :text-data="item.PART101.risk_value"
                :show-risk-label="false"/>
          </td>
          <td>{{ item.PART101.risk_value.split(':')[0] + '%' }}</td>
          <td>{{ item.PART102.risk_value }}</td>
          <td>{{ dayjs(item.PART103.risk_value).format('YYYY-MM-DD') }}</td>
          <td>{{ item.PART104.risk_value }}</td>
          <td>{{ item.PART105.risk_value }}</td>
          <td v-if="item.PART106">{{ item.PART106.risk_value }}</td>
        </tr>
        </tbody>
      </table>

      <PanelTitle title="영상 데이터 분석 위험도"/>

      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2">날짜</th>
          <th rowspan="2" class="w300">{{ ksecTitles.KSEC101 }}</th>
          <th rowspan="2">{{ ksecTitles.KSEC102  }}</th>
          <th rowspan="2">{{ ksecTitles.KSEC103  }}</th>
          <th colspan="3">이상원인</th>
        </tr>
        <tr>
          <th>{{ ksecTitles.KSEC105 }}</th>
          <th>{{ ksecTitles.KSEC106 }}</th>
          <th>{{ ksecTitles.KSEC107  }}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, index) in ksecDataList" :key="index">
          <td>{{ dayjs(item.KSEC101.ymd).format('M/DD') }}</td>
          <td class="flex-in-td">
            <RiskLabel
                v-if="item.KSEC101"
                :text-data="item.KSEC101.risk_value"/>
            <button v-if="item.KSEC103.risk_value"
                    @click="popupAerialPhoto(item)"
               class="default-button">
              항공사진
            </button>
          </td>
          <td>
            <RiskLabel
                v-if="item.KSEC102.risk_value"
                :text-data="item.KSEC102.risk_value"/>
          </td>
          <td>
            <RiskLabel
                v-if="item.KSEC103.risk_value"
                :text-data="item.KSEC103.risk_value"/>
          </td>
          <td>
            <RiskLabel
                v-if="item.KSEC105.risk_value"
                :text-data="item.KSEC105.risk_value"
                is-delete-step-suffix/>
          </td>
          <td>
            <RiskLabel
                v-if="item.KSEC106.risk_value"
                :text-data="item.KSEC106.risk_value"
                is-delete-step-suffix/>
          </td>
          <td>
            <RiskLabel
                v-if="item.KSEC107.risk_value"
                :text-data="item.KSEC107.risk_value"
                is-delete-step-suffix/>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    <AerialPhotoViewer v-if="isShowViewer" @close="onClosePopup" />
  </div>
</template>

<script>
import PageTitle from "@/components/panels/PageTitle";
import dayjs from "dayjs";
import formatter from "@/modules/formatter";
import PanelTitle from "@/components/ui/PanelTitle";
import ChartBox from "@/components/chart/ChartBox";
import {mapState} from "vuex";
import RiskLabel from "@/components/ui/RiskLabel";
import AerialPhotoViewer from "@/components/ui/AerialPhotoViewer";
export default {
  name: "RiskPredictionEquipmentDetail",
  components: {AerialPhotoViewer, RiskLabel, ChartBox, PanelTitle, PageTitle},
  props: {
    eqmt_id: String
  },
  computed: {
    ...mapState([
      'riskEquipmentCrigHistory',
      'riskEquipmentPartDbHistory',
      'riskEquipmentKsec3History',
    ]),
    dayDataList(){
      let list = [];
      let currentDay = null, rowList = [];

      this.riskEquipmentCrigHistory.list.forEach(item => {
        if (currentDay !== item.ymd) {
          currentDay = item.ymd;
          rowList = [];
          list.push(rowList);
          this.dateList.push(item.ymd)
        }
        let i = this.timeListData.indexOf(item.qtime);
        rowList[i] = item;
      })
      return list;
    },
    ksecTitles(){
      let obj = {};
      this.riskEquipmentKsec3History.list.forEach(item => {
        obj[item.risk_type_id] = item.risk_type_nm;
      })
      return obj;
    },
    ksecDataList(){
      let list = [];
      let currentDay = null, rowObj = {};
      this.riskEquipmentKsec3History.list.forEach(item => {
        if (currentDay !== item.ymd){
          currentDay = item.ymd
          rowObj = {};
          list.push(rowObj)
        }
        rowObj[item.risk_type_id] = item;
      })
      return list;
    },
    partTitles(){
      let obj = {};
      this.riskEquipmentPartDbHistory.list.forEach(item => {
        obj[item.risk_type_id] = item.risk_type_nm;
      })
      return obj;
    },
    partDataList(){
      let list = [];
      let currentDay = null, rowObj = {};
      this.riskEquipmentPartDbHistory.list.forEach(item => {
        if (currentDay !== item.ymd){
          currentDay = item.ymd
          rowObj = {};
          list.push(rowObj)
        }
        rowObj[item.risk_type_id] = item;
      })
      return list;
    },
    processTitle(){
      return (this.$route.query.procs_nm) ? this.$route.query.procs_nm : '상세 정보';
    },
    equipmentTitle(){
      return (this.$route.query.eqmt_nm) ? this.$route.query.eqmt_nm : '상세 정보';
    },

  },

  created() {
    this.queryRiskEquipmentCrigHistory();
  },

  data(){
    return {
      dayjs,
      formatter,
      selectedDate: {
        startDate: dayjs().format('YYYYMMDD'),
        endDate: dayjs().format('YYYYMMDD')
      },
      timeListData: ['00', '03', '06', '09', '12', '15', '18', '21'],
      dateList : [],
      partListData: ['PART101', 'PART102', 'PART103', 'PART104', 'PART105', 'PART106'],
      ksecListData: ['KSEC101', 'KSEC102', 'KSEC103', 'KSEC104', 'KSEC105', 'KSEC106', 'KSEC107'],
      isShowViewer: false
    }
  },

  methods: {
    queryRiskEquipmentCrigHistory(){
      this.$store.dispatch('riskEquipmentCrigHistory', {queryString: '?strt_anys_ymd=' + this.selectedDate.startDate + '&end_anys_ymd=' + this.selectedDate.endDate + '&eqmt_id=' + this.eqmt_id})
      this.$store.dispatch('riskEquipmentPartDbHistory', {queryString: '?anys_ymd=' + this.selectedDate.startDate + '&end_anys_ymd=' + this.selectedDate.endDate + '&eqmt_id=' + this.eqmt_id})
      this.$store.dispatch('riskEquipmentKsec3History', {queryString: '?anys_ymd=' + this.selectedDate.startDate + '&end_anys_ymd=' + this.selectedDate.endDate + '&eqmt_id=' + this.eqmt_id})
      // this.$store.dispatch('riskEquipmentCrigHistory', {queryString: '?strt_anys_ymd=20200929&end_anys_ymd=20201001&eqmt_id=E00001'})
      // this.$store.dispatch('riskEquipmentPartDbHistory', {queryString: '?anys_ymd=20200929&end_anys_ymd=20201001&eqmt_id=E00001'})
      // this.$store.dispatch('riskEquipmentKsec3History', {queryString: '?anys_ymd=20200929&end_anys_ymd=20201001&eqmt_id=E00001'})
    },
    getTableHeader(str){
      return (str === 'date') ? '날짜' : str + '시';
    },
    onChangedDate(range){
      console.log(range, 'onChangedDate<<<<')
      this.dateList.length = 0;
      this.selectedDate.startDate = dayjs(range.startDate).format('YYYYMMDD');
      this.selectedDate.endDate = dayjs(range.endDate).format('YYYYMMDD');
      this.queryRiskEquipmentCrigHistory();
    },
    crigYmdList() {
      let list = [];
      this.riskEquipmentCrigHistory.list.forEach(item => {
        let temp = list[list.length - 1];
        if (temp && temp.ymd === item.ymd){
          temp.count++;
        } else {
          list.push({
            ymd: item.ymd,
            count: 1
          });
        }
      })
      return list
    },
    popupAerialPhoto(info){
      console.log(info)
      this.isShowViewer = !this.isShowViewer
    },
    onClosePopup(){
      this.isShowViewer = false;
    }
  }
}
</script>

<style scoped lang="scss">
@import "src/assets/scss/table";
</style>